@use 'abstracts';
@use 'components';

/* =================================================================
 * Definições Customizadas
 * ================================================================= 
 * 
 */

html {
  background-color: var(--surface1);
  color: var(--text1);
  
}

body {
  font-family: var(--font-sans-serif);
}

#text-brand {
  color: var(--brand);
}

.brand {
  color: var(--text1);
  background-color: var(--brand);
}

.surface1 {
  background-color: var(--surface1);
  color: var(--text2);
}

.surface2 {
  background-color: var(--surface2);
  color: var(--text2);
}

.surface3 {
  background-color: var(--surface3);
  color: var(--text1);
}

.surface4 {
  background-color: var(--surface4);
  color: var(--text1);
}

.text1 {
  color: var(--text1);
}

.text2 {
  color: var(--text2);
}
